import CFooter from "@/components/cFooter.vue";
import PAvatar from '@/popup/pAvatar.vue';
export default {
  components: { CFooter, PAvatar },
  middleware: "auth",
  data() {
    return {
      inputList: {
        nick: {
          value: null,
          validation: () => {
            return this.checkNick();
          },
        },
      },
      bPop: {
        isCreate: false,
        isShow: false,
      },
    };
  },
  watch: {},
  created() {},
  mounted() {},
  activated() {},
  deactivated() {},
  destroyed() {},
