    checkNick() {
      if (!this.inputList.nick.value) {
        this.showToast("닉네임을 입력해주세요");
        return false;
      }
      return true;
    },
    onClickActionProfile() {
      this.onShowPopupAction(this.bPop)
    }
  },
};
</script>
